import styled from "styled-components";

const Project = (props) => {
  return (
    <>
      <Pro
        key={props.name}
        style={{
          backgroundColor: "#36175e",
          backgroundBlendMode: "screen",
          backgroundImage: props.thumbnail,
        }}
      >
        {props.name}
        <Icons>
          <LinkColour>
            <i className="fas fa-link"></i>
          </LinkColour>
          <LinkColour href={props.link}>
            <i className="fab fa-github fa-lg"></i>
          </LinkColour>
        </Icons>
      </Pro>
    </>
  );
};

const Pro = styled.div`
  color: #9768d1;
  min-width: 330px;
  min-height: 300px;
  padding: 10px 10px 0 10px;
  margin: 10px 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
`;

const Icons = styled.div`
  display: flex;
  flex-direction: row;
  position: relative;
  bottom: -250px;
  padding-left: 280px;
`;

const LinkColour = styled.a`
  display: block;
  color: #9768d1;
  padding: 3px;
`;

export default Project;
